  }
  
  /* „Éö„Éº„Ç∏„Çø„Ç§„Éà„É´ */
  .admin-container-kategori .admin-category-header {
    text-align: center;
    font-size: 24px;
    font-weight: bold;
    margin-bottom: 20px;
    color: #b30000;
  }
  
  .admin-container-kategori .admin-kotoba {
    text-align: center;
    font-size: 24px;
    font-weight: bold;
    margin-bottom: 20px;
    color: #b30000;
  }
  
  /* „Ç´„ÉÜ„Ç¥„É™„Éú„Çø„É≥„ÅÆ„Ç∞„É™„ÉÉ„Éâ */
  .admin-container-kategori .admin-category-container {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 20px;
