  /* Ë≥™ÂïèÁôªÈå≤„Éú„Çø„É≥ */
  .admin-container-kategori .reg {
    display: block; /* „Ç§„É≥„É©„Ç§„É≥Ë¶ÅÁ¥†„Çí„Éñ„É≠„ÉÉ„ÇØË¶ÅÁ¥†„Å´Â§âÊõ¥ */
    width: 100%; /* Ë¶™Ë¶ÅÁ¥†„ÅÑ„Å£„Å±„ÅÑ„Å´Â∫É„Åí„Çã */
    max-width: 900px; /* ÂπÖ„ÇíÊúÄÂ§ß„Å´Ë®≠ÂÆö */
    padding: 15px 20px; /* „Éë„Éá„Ç£„É≥„Ç∞„ÇíÂ∞ë„ÅóÂ∫É„ÇÅ„Å´ */
    background-color: #8B0000; /* ÊøÉ„ÅÑËµ§Ëâ≤ („ÉÄ„Éº„ÇØ„É¨„ÉÉ„Éâ) */
    text-align: center;
    color: white; /* ÊñáÂ≠óËâ≤„ÇíÁôΩ„Å´ */
    border: none; /* „Éú„Éº„ÉÄ„Éº„Å™„Åó */
    border-radius: 10px; /* Ëßí„ÇíÂ∞ë„Åó‰∏∏„Åè */
    font-size: 18px; /* „Éï„Ç©„É≥„Éà„Çµ„Ç§„Ç∫„ÇíÂ§ß„Åç„Åè */
    font-weight: bold; /* Â§™Â≠ó„Å´Ë®≠ÂÆö */
    cursor: pointer; /* „Éù„Ç§„É≥„ÇøË°®Á§∫ */
    transition: background-color 0.3s ease, transform 0.2s ease; /* „Éõ„Éê„ÉºÊôÇ„ÅÆ„Éà„É©„É≥„Ç∏„Ç∑„Éß„É≥ */
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2); /* ÂΩ±„ÇíËøΩÂä†„Åó„Å¶Á´ã‰ΩìÊÑü */
    text-transform: uppercase; /* „ÉÜ„Ç≠„Çπ„Éà„ÇíÂ§ßÊñáÂ≠ó„Å´ */
    letter-spacing: 1px; /* ÊñáÂ≠óÈñìÈöî„ÇíÂ∞ë„ÅóÂ∫É„Åè */
    margin: 20px auto; /* ‰∏ä‰∏ã20px„ÅÆ‰ΩôÁôΩ„ÇíË®≠ÂÆö„Åó„ÄÅ‰∏≠Â§ÆÊèÉ„Åà */
    text-align: center;
  }
  
  .admin-container-kategori .reg:hover {
    background-color: #660000; /* „Åï„Çâ„Å´ÊøÉ„ÅÑËµ§„Å´Â§âÊõ¥Ôºà„Éõ„Éê„ÉºÊôÇÔºâ */
    transform: translateY(-3px); /* „Éõ„Éê„ÉºÊôÇ„Å´Â∞ë„ÅóÊµÆ„Åç‰∏ä„Åå„Çã */
    box-shadow: 0 6px 14px rgba(0, 0, 0, 0.3); /* „Éõ„Éê„ÉºÊôÇ„ÅÆÂΩ±„ÇíÂº∑Ë™ø */
  }
  
  .admin-container-kategori .reg:active {
    background-color: #4B0000; /* „ÇØ„É™„ÉÉ„ÇØÊôÇ„ÅØ„Åï„Çâ„Å´ÊøÉ„ÅÑËâ≤„Å´ */
    transform: translateY(0); /* „ÇØ„É™„ÉÉ„ÇØÊôÇ„Å´ÂÖÉ„ÅÆ‰ΩçÁΩÆ„Å´Êàª„Åô */
    box-shadow: 0 3px 8px rgba(0, 0, 0, 0.2); /* ÂΩ±„ÇíÂ∞ë„ÅóÂº±„ÇÅ„Çã */
  }
  
  .admin-container-kategori .reg:disabled {
    background-color: #ccc; /* ÁÑ°ÂäπÊôÇ„ÅÆ„Ç∞„É¨„Éº */
    color: #777; /* ÊñáÂ≠ó„ÇÇËñÑ„Åè */
    cursor: not-allowed; /* ÁÑ°ÂäπÊôÇ„ÅÆ„Ç´„Éº„ÇΩ„É´ */
    box-shadow: none; /* ÂΩ±„ÇíÂâäÈô§ */
  }
  
  @media (max-width: 768px) {
    .admin-container-kategori {
