  /* „Éï„Çß„Éº„Éâ„Ç§„É≥„Ç¢„Éã„É°„Éº„Ç∑„Éß„É≥ */
  @keyframes fadeIn {
    0% {
      opacity: 0;
      transform: translateY(20px);
    }
  
    100% {
      opacity: 1;
      transform: translateY(0);
    }
  
  
  }/* Ë≥™ÂïèÁôªÈå≤„Éï„Ç©„Éº„É†„ÅÆ„Ç≥„É≥„ÉÜ„Éä */
  .register-modal {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.5);
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .register-container {
    font-family: 'Open Sans', sans-serif;
    max-width: 800px; /* ÂπÖ„ÇíÂ∫É„ÅèË®≠ÂÆö */
    width: 90%; /* ÂπÖ„Çí90%„Å´Ë®≠ÂÆö */
    margin: 20px auto;
    padding: 20px;
    background: #ffdddd;
    border-radius: 12px;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
    animation: fadeIn 0.8s ease-out;
    
  }
  
  /* „Éï„Ç©„Éº„É†„ÅÆ„Çø„Ç§„Éà„É´ */
  .register-container h1 {
    font-size: 24px;
    margin-bottom: 20px;
