    text-align: center; /* ÊñáÂ≠ó„Çí‰∏≠Â§Æ„Å´ÈÖçÁΩÆ */
  }
  
  /* „Ç®„É©„Éº„É°„ÉÉ„Çª„Éº„Ç∏ */
  .register-container .error-message {
    color: red;
