    pointer-events: auto; /* „ÇØ„É™„ÉÉ„ÇØ„Çí„Éú„Çø„É≥ÂÜÖ„ÅÆ„Åø„Å´Âà∂Èôê */
  }
  
  /* „Çª„É¨„ÇØ„Éà„Éú„ÉÉ„ÇØ„Çπ„ÄÅ„ÉÜ„Ç≠„Çπ„Éà„Ç®„É™„Ç¢„ÄÅ„Ç§„É≥„Éó„ÉÉ„Éà */
  .register-container select,
  .register-container textarea,
  .register-container input {
    width: 100%;
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 8px;
    font-size: 16px;
    margin-bottom: 20px;
  }
  
  /* „ÉÜ„Ç≠„Çπ„Éà„Ç®„É™„Ç¢„ÅÆÈ´ò„Åï */
  .register-container textarea {
    resize: vertical;
