  }
  
  /* „Éà„Ç∞„É´„Çπ„Ç§„ÉÉ„ÉÅ„ÅÆ„É©„ÉÉ„Éë„Éº */
  .register-container .toggle-wrapper {
    margin-top: 10px;
    gap: 10px;
    z-index: 999; /* ‚úÖ „É°„Éã„É•„Éº„Ç™„Éº„Éê„Éº„É¨„Ç§„ÅÆ‰∏ã„Å´„Åô„Çã */
  }
  
  
  .toggle-container {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    justify-content: flex-end;
    gap: 8px;
  }
  
  
  /* ÂÖ¨Èñã/ÈùûÂÖ¨Èñã„ÅÆ„É©„Éô„É´ */
  .toggle-text {
    font-size: 16px;
    font-weight: bold;
    color: #0056b3;
    white-space: nowrap;
    position: relative;
    justify-content: flex-end;
    z-index: 2; /* ‚úÖ „Çπ„Ç§„ÉÉ„ÉÅ„ÅÆ‰∏ã„Å´Èö†„Çå„Å™„ÅÑ„Çà„ÅÜ„Å´„Åô„Çã */
    
  }
  
  /* „Éà„Ç∞„É´„Çπ„Ç§„ÉÉ„ÉÅ */
  .register-container .toggle-switch {
    width: 50px;
    height: 25px;
