    z-index: 1; /* ‚úÖ „É©„Éô„É´„ÅÆ‰∏ã„Å´„Å™„Çâ„Å™„ÅÑ„Çà„ÅÜ„Å´„Åô„Çã */
  }
  
  .register-container .toggle-switch.active {
