  /* „Ç´„ÉÜ„Ç¥„É™ÈÅ∏Êäû„Éù„ÉÉ„Éó„Ç¢„ÉÉ„Éó */
  .category-modal {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.5);
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .category-modal-content {
    background: #fff;
    padding: 20px;
    border-radius: 12px;
    max-width: 500px;
    width: 100%;
  }
  
  .category-grid {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(100px, 1fr));
    gap: 10px;
    margin-top: 20px;
  }
  
  .category-option-button {
    padding: 10px;
    background-color: #f0f0f0;
    border: none;
    border-radius: 8px;
    cursor: pointer;
    transition: background-color 0.3s ease;
  }
  
  .category-option-button:hover {
    background-color: #ddd;
  }
  
  .category-option-button:disabled {
    background-color: #ccc;
    cursor: not-allowed;
  }
  
  .modal-close-button {
    margin-top: 20px;
    padding: 10px 20px;
    background-color: #ff6666;
    color: white;
    border: none;
    border-radius: 8px;
    cursor: pointer;
    transition: background-color 0.3s ease;
  }
  
  .modal-close-button:hover {
    background-color: #ff3333;
  }/* „Çø„Ç§„Éà„É´„Éú„Çø„É≥ */
  
