}

/* -----------------------
   „Çπ„É¨„ÉÉ„Éâ‰∏ÄË¶ßÔºà„Éâ„É≠„ÉØ„ÉºÔºâ
------------------------ */
.chat-sidebar.drawer {
  position: fixed;
  top: 0;
  left: -280px;
  width: 280px;
  height: 100vh;
  background: #ffffff;
  border-right: 1px solid #e5e5e5;
  padding: 120px 12px 12px 12px; /* ‰∏äÈÉ®„Å´„Éè„É≥„Éê„Éº„Ç¨„Éº„É°„Éã„É•„ÉºÂàÜ„ÅÆ„Çπ„Éö„Éº„Çπ„ÇíÁ¢∫‰øù */
  overflow-y: auto;
  z-index: 999;
  transition: left 0.3s ease;
  box-shadow: 2px 0 12px rgba(0, 0, 0, 0.15);
  /* Á¢∫ÂÆü„Å´Ë°®Á§∫„Åï„Çå„Çã„Çà„ÅÜ„Å´Ë®≠ÂÆö */
  display: block !important;
  visibility: visible !important;
}

.chat-sidebar.drawer.open {
  left: 0;
}

.sidebar-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 8px;
}

.header-buttons {
