  flex-direction: column;
  justify-content: space-around;
  width: 24px;
  height: 18px;
  background: none;
  border: none;
  cursor: pointer;
  padding: 0;
}

.hamburger-btn span {
  display: block;
  height: 2px;
  width: 100%;
  background-color: #374151;
  border-radius: 1px;
  transition: all 0.3s ease;
}

.hamburger-btn:hover span {
  background-color: #1f2937;
}

/* „ÉÅ„É£„ÉÉ„Éà„Ç®„É™„Ç¢ÂÜÖ„ÅÆ„Çπ„É¨„ÉÉ„Éâ„Éú„Çø„É≥ */
.chat-hamburger {
  position: relative; /* absolute„Åã„Çârelative„Å´Â§âÊõ¥ */
  background: rgba(255, 255, 255, 0.95);
  border: 1px solid #e5e5e5;
  border-radius: 8px;
  padding: 14px; /* „Éë„Éá„Ç£„É≥„Ç∞„Çí10px„Åã„Çâ14px„Å´Â¢óÂä† */
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
  transition: all 0.2s ease;
  display: flex;
  align-items: center;
  justify-content: center;
  min-width: 44px; /* ÊúÄÂ∞èÂπÖ„ÇíË®≠ÂÆö */
  min-height: 44px; /* ÊúÄÂ∞èÈ´ò„Åï„ÇíË®≠ÂÆö */
}

/* „ÉÅ„É£„ÉÉ„Éà„Ç®„É™„Ç¢ÂÜÖ„ÅÆ„Éú„Çø„É≥„Ç≥„É≥„ÉÜ„Éä */
.chat-controls {
  position: absolute;
  top: 12px;
  left: 12px;
  z-index: 10;
  display: flex;
  gap: 12px;
  align-items: center;
  flex-direction: row; /* Ê®™‰∏¶„Å≥„ÇíÊòéÁ§∫ÁöÑ„Å´ÊåáÂÆö */
  flex-wrap: nowrap; /* Êäò„ÇäËøî„Åó„ÇíÈò≤„Åê */
}

.widthToggle {
  display: inline-flex;
  background: #fff;
  border: 1px solid #d1d5db;
  border-radius: 8px;
  overflow: hidden;
  box-shadow: 0 1px 2px rgba(0,0,0,0.05);
}

.widthBtn {
  padding: 6px 10px;
  font-size: 12px;
  color: #374151;
  background: transparent;
  border: none;
  cursor: pointer;
}

.widthBtn + .widthBtn {
