  pointer-events: none; /* Ëá™Ë∫´„ÅØ„ÇØ„É™„ÉÉ„ÇØÈÄèÈÅé */
}

.chat-widthbar .widthToggle {
  pointer-events: auto; /* „Éú„Çø„É≥„ÅØ„ÇØ„É™„ÉÉ„ÇØÂèØËÉΩ„Å´ */
