/* -----------------------
   È°û‰ººË≥™ÂïèÔºàRAGÔºâË°®Á§∫
------------------------ */
.rag-details {
  margin-top: 10px;
  border: 1px solid #e5e7eb;
  border-radius: 10px;
  background: #f9fafb;
  overflow: hidden;
}

.rag-details[open] {
  background: #ffffff;
  border-color: #d1d5db;
}

.rag-summary {
  list-style: none;
  cursor: pointer;
  padding: 10px 12px;
  font-weight: 600;
  font-size: 14px;
  color: #0f172a;
  background: linear-gradient(0deg, rgba(255,255,255,0.9), rgba(255,255,255,0.9));
  border-bottom: 1px solid #e5e7eb;
}

.rag-summary::-webkit-details-marker {
  display: none;
}

.rag-summary::before {
  content: "‚ñ∂";
  display: inline-block;
  margin-right: 8px;
  color: #2563eb;
  transition: transform 0.15s ease;
}

.rag-details[open] .rag-summary::before {
  transform: rotate(90deg);
}

.rag-list {
  list-style: none;
  margin: 0;
  padding: 8px 10px 12px 10px;
  display: grid;
  gap: 10px;
}

.rag-item {
  border: 1px solid #e5e7eb;
  border-radius: 8px;
  background: #ffffff;
  box-shadow: 0 1px 2px rgba(0,0,0,0.04);
  overflow: hidden;
}

.rag-item:hover {
  border-color: #d1d5db;
  box-shadow: 0 2px 6px rgba(0,0,0,0.06);
}

.rag-q-summary,
.rag-answer {
  position: relative;
  padding-left: 28px;
  line-height: 1.5;
  color: #111827;
  white-space: pre-wrap;
}

.rag-q-summary {
  list-style: none;
  cursor: pointer;
  display: block;
  padding: 10px 12px 10px 28px;
  font-weight: 600;
  background: #f9fafb;
  border-bottom: 1px solid #f1f5f9;
}

.rag-q-summary::before,
.rag-answer::before {
  position: absolute;
  left: 0;
  top: 0;
  font-size: 12px;
  font-weight: 700;
  padding: 2px 6px;
  border-radius: 6px;
  line-height: 1.2;
}

.rag-q-summary::before {
  content: "Q";
  color: #1d4ed8;
  background: #dbeafe;
  border: 1px solid #bfdbfe;
}

.rag-answer::before {
  content: "A";
  color: #059669;
  background: #d1fae5;
  border: 1px solid #a7f3d0;
}

.rag-time {
  margin-top: 6px;
  font-size: 12px;
  color: #6b7280;
  padding: 0 12px 10px 28px;
}

/* „É¢„Éê„Ç§„É´ÊúÄÈÅ©Âåñ */
@media (max-width: 768px) {
  .rag-summary { font-size: 13px; padding: 9px 10px; }
  .rag-q-summary, .rag-answer { padding-left: 26px; }
}
